import { EventEmitter } from 'events';
import {
  CreateI18n,
  PlatformShowError,
  PlatformShowErrorProps,
  StatusCode,
  StatusCodeNumeric,
} from '@wix/error-handler-core/types';
import { TypedEmitter } from '../util/typedEmitter';
import { FunctionMock } from '../util/FunctionMock';
import { ErrorHandlerTestkitPublicAPI } from './ErrorHandlerTestkitPublicAPI';
import { resolveFromMapping } from '../util/resolveFromMapping';
import { TranslationsState } from '../state/TranslationsState';
import { CommonErrorsMapState } from '../state/CommonErrorsMapState';
import { ErrorAccessor } from '../state/ErrorAccessor';
import { statusCodeTextToNumeric } from '../util/statusCodeTextToNumeric';
import { fallbackMessage } from '../fallbackMessage';

export interface ErrorHandlerTestkitParams {
  language: string;
  createI18n: CreateI18n;
}

function isInteger(value: unknown): value is number {
  return Number.isInteger(value);
}

export class ErrorHandlerTestkitBase implements ErrorHandlerTestkitPublicAPI {
  readonly events = new EventEmitter() as TypedEmitter<{
    showError: () => PlatformShowErrorProps;
  }>;

  readonly translations;
  readonly commonErrorsMap;

  constructor(params: ErrorHandlerTestkitParams) {
    this.translations = new TranslationsState({
      createI18n: params.createI18n,
    });

    this.commonErrorsMap = new CommonErrorsMapState({
      translations: this.translations,
      errorMonitor: {
        captureException: () => {},
      },
    });
  }

  showError: PlatformShowError = (props) => {
    const { events } = this;
    events.on('showError', () => props);
  };

  get showErrorMock(): FunctionMock<PlatformShowErrorProps> {
    const { events } = this;
    return {
      get calls() {
        return events.listeners('showError').map((cb) => cb());
      },
      get latest() {
        const listeners = events.listeners('showError');
        return listeners[listeners.length - 1]?.();
      },
    };
  }

  getCommonErrorMessageByStatus = async (
    status: StatusCode | StatusCodeNumeric,
  ): Promise<string | undefined> => {
    const { commonErrorsMap } = this;
    await commonErrorsMap.getInitPromise();

    if (!commonErrorsMap.commonErrors) {
      return undefined;
    }

    const httpError = Object.assign(new Error(), {
      name: '',
      message: '',
      response: {
        status: isInteger(status)
          ? status
          : statusCodeTextToNumeric[status] ?? 0,
        statusText: '',
        data: {},
        headers: {},
        config: {},
      },
      config: {},
      toJSON: () => ({}),
      __CANCEL__: false,
    });

    return resolveFromMapping({
      errorCodesMap: commonErrorsMap.commonErrors.commonErrorsMap,
      errorAccessor: new ErrorAccessor({ error: httpError }),
    })?.message;
  };

  async getClientErrorMessages() {
    const { translations } = this;
    const t = await translations.getTranslateFn();
    return {
      networkError: t('errorHandler.common-error.networkError'),
      timeoutExceeded: t('errorHandler.common-error.technicalIssue.generic'),
      requestAborted: t('errorHandler.common-error.technicalIssue.generic'),
      otherNetworkError: t(
        'errorHandler.common-error.technicalIssue-unmappedError',
      ),
      noConnection: t('errorHandler.common-error.noConnection'),
      fallbackMessage: t('errorHandler.common-error.technicalIssue'),
      fallbackMessageEn: fallbackMessage,
    };
  }
}
